@import "reset";
@import "vars";
@import "mixins";

/* sanomat-regular*/

@font-face {
    font-family: 'Sanomat';
    font-style: normal;
    font-weight: 400;
    src: url('../fonts/sanomat/SanomatGrabRegular.eot');
    /* IE9 Compat Modes */
    src: local('Sanomat Regular'), local('Sanomat-Regular'), url('../fonts/sanomat/SanomatGrabRegular.woff2') format('woff2'), /* Super Modern Browsers */
    url('../fonts/sanomat/SanomatGrabRegular.woff') format('woff'), /* Modern Browsers */
}

body,
html {
    margin: 0;
    padding: 0;
    overflow: hidden;
}

.wrapper {
    width: 100%;
    padding: 0;
    position: relative;
    min-height: 100vh;
    font-family: Sanomat, Arial, sans-serif;
    overflow-x: hidden;
    background-color: #C2EAF3;
    // @include rfs(16); // Responsive Font-Size
}

.top-bar-mobile {
    display: none;
}

.logo-grab {
    position: absolute;
    top: 0;
    left: 10%;
    width: 34%;
    height: 25%;
    @media (max-width: 480px) {
        display: block;
        position: static;
        width: 80%;
        margin: auto;
        margin-top: 35px;
        z-index: 0;
    }
}

.wrapper svg.background {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.leaderboard {
    background-color: #00953a;
    border-radius: 26px;
    position: absolute;
    right: 2.4%;
    top: 52px;
    width: 257px;
    // overflow: hidden;
}

.leaderboard img {
    height: auto;
    width: 100%;
}

@media (max-width: 480px) {
    .top-bar-mobile {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100vw;
        height: 50px;
        background: green;
        z-index: 1;
    }
    .background {
        display: none!important;
    }
    .leaderboard {
        margin: 0 auto;
        left: 0;
        right: 0;
        margin-top: 140px;
    }
}

.logo-outer span.first-com {
    position: absolute;
    top: 11px;
    margin-left: 9px;
}

.logo-outer span.second-com {
    position: absolute;
    top: 13px;
    margin-left: 16px;
}

.logo-outer span.three-com {
    position: absolute;
    top: 13px;
    margin-left: 16px;
}

.board-outer.second,
.board-outer.third {
    // padding-top: 10px;
}

.board-outer {
    border-top: 1px solid;
    line-height: 0;
}

.leaderboard>.board-outer:first-child {
    border-top: medium none;
    position: relative;
    &:before {
        content: "";
        position: absolute;
        top: -40px;
        left: -5%;
        margin: 0 auto;
        width: 120%;
        height: 50px;
        background: url("../img/board/rogy.svg") no-repeat;
    }
}

.board-inner {
    display: inline-block;
    padding: 10px 20px 25px;
    position: relative;
    width: 100%;
    box-sizing: border-box;
}

.board-outer.active .board-inner {
    padding: 31px 20px;
}

.board-outer .board-inner .logo {
    width: 27px;
    height: 27px;
    margin-left: 5px;
    float: left;
}

.board-outer.active .board-inner .logo {
    width: 15%;
    float: left;
}

.board-outer .board-inner span {
    font-size: 20px;
    color: #FFDD00;
}

.board-outer.second .board-inner span {
    color: #E2E2E2;
}

.board-outer.third .board-inner span {
    color: #E27037;
}

.company {
    padding: 15px 20px;
    width: 100%;
    box-sizing: border-box;
}

.company .logo {
    width: 37px;
    height: 37px;
    display: inline-block;
    box-sizing: border-box;
}

.company .company-content {
    width: 81%;
    float: right;
    padding-left: 13px;
    box-sizing: border-box;
}

.board-outer.active .company {
    background-color: #50BC7C;
}

.company .company-content h2 {
    color: #ffffff;
    font-size: 10pt;
    font-weight: bold;
    margin: 0 0 7px;
    line-height: 1.1;
}

.company .company-content span {
    color: #fff;
    font-size: 10px;
}

.progress {
    margin-bottom: 9px;
    height: 4px;
    background-color: #000;
}

.progress-bar {
    background-color: #FFDD00;
    height: 100%;
}

.logo-outer {
    display: inline-block;
    position: absolute;
    width: 100%;
    box-sizing: border-box;
}

.viewmoreimage {
    width: 27px;
    height: 30px;
    float: right;
}

.view-more {
    text-align: right;
    padding: 15px 20px;
}

.view-more span {
    color: rgba(255, 255, 255, 0.5);
    font-size: 15px;
    font-weight: 700;
    padding-right: 10px;
}

.read_more {
    cursor: pointer;
}

.board-outer>img {
    border-radius: 12px;
}

.board-outer.active {
    border: none;
}